$header-top-gap: 82px;
$header-logo-height: 51px;
$header-navigation-size: 25px;
$header-navigation-container-gap: 55px;

.header {
	position: fixed;
	z-index: 10;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: $header-top-gap;
	padding-bottom: 20px;
	@include r(374) {
		padding-top: 50px; }
	&__container {}
	&__content {
		display: flex;
		align-items: stretch;
		justify-content: space-between; }
	&__logo {
		width: 62px;
		height: $header-logo-height;
		color: $white;
		svg {
			width: 100%;
			height: 100%; } }
	&__navigation {
		position: relative;
		display: flex;
		align-items: center;
		width: $header-navigation-size; }
	&__burger {
		display: none;
		outline: none;
		position: relative;
		z-index: 2;
		width: $header-navigation-size;
		height: $header-navigation-size;
		border: 0;
		cursor: pointer;
		background-color: transparent;
		@include r(1365) {
			display: block; }
		&.is-active {
			.header__burger-line {
				&:nth-child(1) {
					opacity: 0; }
				&:nth-child(2) {
					transform: translate(0%, 0) rotate(45deg); }
				&:nth-child(3) {
					transform: translate(0%, 0) rotate(-45deg);
					top: 50%; } }
			@include r(1365) {
				.header__burger-line {
					background-color: $dark-purple; } } } }
	&__burger-line {
		position: absolute;
		display: block;
		width: 100%;
		height: 2px;
		background-color: $white;
		left: 0;
		transition: background-color $transition-time, transform $transition-time;
		&:nth-child(1) {
			top: 0; }
		&:nth-child(2) {
			top: 50%;
			transform: translateY(-50%); }
		&:nth-child(3) {
			bottom: 0; } }

	&__navigation-container {
		position: absolute;
		top: -82px;
		right: 0;
		height: 100vh;
		width: 270px;
		transform: translateX(211px);
		display: flex;
		align-items: center;
		padding: 60px 0 100px;
		transition: transform 0.25s;
		@include r(1919) {
			position: fixed;
			top: 0;
			right: 0;
			transform: translateX(0);
			width: 290px; }
		@include r(1365) {
			transform: translateX(100%);
			width: 260px; }
		&.is-active {
			@include r(1365) {
				transform: translateX(0); } }
		&.first-section {
			.header__navigation-list-item-link {
				opacity: 1;
				transform: translate(0, -50%);
				@include r(1365) {
					transform: translate(0, 0); } } }
		&.is-black {
			.header__navigation-list-item {
				color: $dark-purple;
				&:hover {
					color: $yellow;
					.header__navigation-list-item-line {
						background-color: $yellow; } }
				@include r(1365) {
					&.is-active {
						color: $yellow;
						.header__navigation-list-item-line {
							background-color: $yellow; } } } }
			.header__navigation-list-item-line {
				background-color: $dark-purple; } } }

	&__navigation-container-wrap {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		padding: 8px 50px 8px 8px;
		@include r(1365) {
			background-color: $white;
			padding: 10px 30px; }
		&:hover {
			.header__navigation-list-item-link {
				opacity: 1;
				transform: translate(0, -50%);
				@include r(1365) {
					transform: translate(0, 0); } } } }
	&__navigation-list-item {
		margin-bottom: 47px;
		counter-increment: menu;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		color: $white;
		@include pr;
		font-size: 16px;
		position: relative;
		transition: color 0.25s;
		&::before {
			content: counter(menu);
			color: inherit;
			font-family: inherit;
			font-size: inherit;
			font-weight: inherit;
			margin-right: 18px;
			transition inherit {} }
		&:last-child {
			margin-bottom: 0; }
		&.is-active {
			.header__navigation-list-item-link {
				opacity: 1;
				transform: translate(0, -50%); } }
		&:hover {
			color: $yellow;
			.header__navigation-list-item-line {
				background-color: $yellow; } }
		@include r(1365) {
			color: $dark-purple;
			&.is-active,
			&:hover {
				color: $yellow;
				.header__navigation-list-item-link {
					opacity: 1;
					transform: translate(0, 0); } }
			&.is-active {
				color: $yellow;
				.header__navigation-list-item-line {
					background-color: $yellow; } } } }
	&__navigation-list-item-line {
		width: 24px;
		height: 1px;
		background-color: $white;
		margin-right: 10px;
		transition: background-color 0.25s;
		@include r(1365) {
			background-color: $dark-purple; } }
	&__navigation-list-item-link {
		color: inherit;
		font-family: inherit;
		font-size: inherit;
		font-weight: inherit;
		position: absolute;
		top: 50%;
		left: 100%;
		transform: translate(15px, -50%);
		white-space: nowrap;
		opacity: 0;
		transition: opacity 0.25s, transform 0.25s;
		@include r(1365) {
			position: relative;
			top: auto;
			left: auto;
			transform: translate(0, 0);
			opacity: 1; } }
	&.is-black {
		.header__logo {
			color: $dark-purple; }
		.header__burger-line {
			background-color: $dark-purple; } } }
