.portfolio {
	background-color: $white;
	padding: 95px 0 85px;
	&__section-title {
		margin-bottom: 70px;
		@include r(1023) {
			margin-bottom: 45px; } }
	&__content {
		@include r(1919) {
			padding-right: 100px; }
		@include r(1599) {
			padding-right: 200px; }
		@include r(1365) {
			padding-right: 0; } }
	&__content-partners {
		margin-bottom: 50px;
		@include r(1023) {
			margin-bottom: 30px; } }
	&__content-partners-title {
		margin-bottom: 5px;
		color: $yellow;
		@include pr;
		font-weight: 300;
		@include r(1023) {
			margin-bottom: 10px; } }
	&__content-partners-tile {
		display: grid;
		grid-template-columns: repeat(6, 1fr);
		grid-auto-rows: 120px;
		gap: 18px;
		@include r(1023) {
			grid-template-columns: repeat(4, 1fr); }
		@include r(639) {
			grid-template-columns: repeat(2, 1fr); } }
	&__content-partners-tile-item {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 10px;
		background-color: #f9f9f9;
		svg,
		img {
			filter: grayscale(1);
			max-width: 100%;
			height: auto;
			display: block; } }
	&__content-statistic {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		margin-bottom: 40px;
		gap: 18px;
		@include r(767) {
			grid-template-columns: 1fr; } }
	&__content-statistic-item-headline {
		@include pr;
		font-weight: 400;
		font-size: 72px; }
	&__content-statistic-item-description,
	&__content-description {
		@include pr;
		font-weight: 300; }
	&__content-line {
		width: 42px;
		height: 1px;
		background-color: $yellow;
		display: block;
		margin-bottom: 42px; }
	&__content-description {
		max-width: 600px; } }

